					serviceCharge: 12,
					cashPledge: 2000,
					propertyFee: 20,
					label: null,
					introduction: '没窗户，安静',
					remark: '两室两厅两卫',
					createBy: '黄鹏飞',
					createDate: '2019-04-26 10:54:28',
					updateBy: '黄鹏飞',
					updateDate: '2019-04-26 10:54:28',
					status: 0,
					pictureList: [{ id: 4, picture: '../../static/img/temp/1.jpg', houseId: 6 }]
				}
			],
			//轮播图下标
			currentSwiper: 0,
