	onLoad: function(option) {
		//option为object类型，会序列化上个页面传递的参数
		uni.request({
			url: 'http://122.112.225.34:8090/house/selectListByParkId',
			data: {
				parkId: option.parkId,
				pageNum: '1',
				pageSize: '99'
			},
			method: 'GET',
			success: res => {
				this.goodsList = res.data.content;
			}
		});
		uni.setNavigationBarTitle({
			title: option.name
		});

		//兼容H5下排序栏位置










		//////////////////////////微信分享////////////////
		let that = this;
		var wx = require('jweixin-module');
		// 获取签名
		uni.request({
			url: 'http://122.112.225.34:8090/wechat/config',
			method: 'GET',
			data: {
				url: 'http://' + window.location.host + '/'
			},
			success: res => {
				wx.config({
					debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
					appId: res.data.appId, // 必填，公众号的唯一标识
					timestamp: res.data.timestamp, // 必填，生成签名的时间戳
					nonceStr: res.data.nonceStr, // 必填，生成签名的随机串
					signature: res.data.signature, // 必填，签名
					jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline'] // 必填，需要使用的JS接口列表
				});

				wx.ready(function() {
					wx.onMenuShareAppMessage({
						title: '租房列表',
						desc: '租房列表',
						link: 'http://yf.179mall.cn:8082/#/pages/temp/temp?parkId=' + option.parkId + '&name=' + option.name,
						imgUrl: 'http://yf.179mall.cn:8082/static/img/temp/zffw.png',
						success: function() {
							console.log('已分享');
						}
					});

					wx.onMenuShareTimeline({
						title: '租房列表', // 分享标题
						link: 'http://yf.179mall.cn:8082/#/pages/temp/temp?parkId=' + option.parkId + '&name=' + option.name,
						// link: that.shareURL, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
						imgUrl: 'http://yf.179mall.cn:8082/static/img/temp/zffw.png', // 分享图标
						success: function() {
							// 用户点击了分享后执行的回调函数
						}
					});
				});
				wx.error(function(res) {
					console.log('wx err', res);
					//可以更新签名
				});
			}
		});
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = 'fixed';
		} else {
			this.headerPosition = 'absolute';
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(() => {
			this.reload();
			uni.stopPullDownRefresh();
		}, 1000);
	},
